.movie-card-short {
    display: flex;
    flex-direction: column;
    max-width: 20%;
    height: 20%;
    margin: 30px;
    height: 100% !important
}

.movie-card-short__header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
    
    
}

.movie-card-short__header__name {
    font-size: 2vw;
    text-align: center;
    /* min-width: 100% !important;
    min-height: 90px; */
    height: 200px;
    max-height: 200px;
    margin-bottom: 40px;
}

.movie-card-short__desc {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    justify-content: center;
}

.movie-card-short__desc__img {
   min-height: inherit !important;
   max-height: inherit !important;
   width: 100%;
    
}

.movie-card-short__header_img-container {
    min-height: 40vh !important;
    max-height: 40vh !important;
    margin-bottom: 20px ;
}

.movie-card-short__desc__text {
    text-align: center;
}


.inner-modal {
   position: absolute;
   width: 20%
}

.container-card-short {
    height: 100%;
}

.movie-card-short__header__addtofavs {
    margin-bottom: 20px !important;
    width: 100% !important;
}

.movie-card-short__header_genres {
    min-height: 60px;
}